import React, { useEffect, useState } from "react";
import Plans from "../supComponents/Plans/Plans";
import FreeTrealBox from "../supComponents/FreeTrealBox/FreeTrealBox";
import ComparisonTable from "../supComponents/ComparisonTable/ComparisonTable";

export default function SubscriptionPage() {
  let [logding, setLoding] = useState(true);
  useEffect(() => {
    setTimeout(() => {
      setLoding(false);
    }, 1000);
  }, []);
  return (
    <>
      {logding === true ? (
        <>
          <div className="logding">
            <span className="loader"></span>
          </div>
        </>
      ) : (
        <></>
      )}
      <Plans />
      <ComparisonTable />
      <FreeTrealBox />
    </>
  );
}
