import React, { useEffect, useState } from "react";
import "./movieDetailsStyle.scss";
import FreeTrealBox from "../supComponents/FreeTrealBox/FreeTrealBox";
import { useDispatch, useSelector } from "react-redux";
import {
  getMovieDetails,
  getMovieKey,
  getMoviesReviews,
} from "../../Redux/movieSlice";
import { useParams } from "react-router-dom";
import MoviesReviews from "../supComponents/MoviesReviews/MoviesReviews";
import ReactPlayer from "react-player";

export default function MovieDetails() {
  let { id } = useParams();
  let dispatch = useDispatch();
  let data = useSelector((state) => state.movieLists.movieDetails);
  let movieKeys = useSelector((state) => state.movieLists.movieKeys);
  let youtubeUrl = `https://www.youtube.com/watch?v=${movieKeys[0]?.key}`;
  let [videoAvail, setVideoAvail] = useState();
  let [logding, setLoding] = useState(true);

  useEffect(() => {
    dispatch(getMovieDetails(id));
    dispatch(getMovieKey(id));
    checkTellier();
    setTimeout(() => {
      setLoding(false);
    }, 1000);
  }, []);

  useEffect(() => {
    dispatch(getMovieDetails(id));
    dispatch(getMovieKey(id));
    dispatch(getMoviesReviews(id));
    checkTellier();
  }, [id]);

  useEffect(() => {
    checkTellier();
    dispatch(getMoviesReviews(id));
  }, [movieKeys]);

  function checkTellier() {
    if (movieKeys[0]?.key === undefined || movieKeys[0]?.key === null) {
      setVideoAvail(false);
    } else {
      setVideoAvail(true);
    }
  }

  const handleButtonClick = (sectionId) => {
    const targetSection = document.getElementById(sectionId); // Get the section element
    if (targetSection) {
      targetSection.scrollIntoView({ behavior: "smooth" }); // Scroll smoothly
    }
  };

  return (
    <>
      {logding === true ? (
        <>
          <div className="logding">
            <span className="loader"></span>
          </div>
        </>
      ) : (
        <></>
      )}
      <div className="container">
        <div className="pictureContainer">
          <img
            height={data.backdrop_path === null ? 600 : "auto"}
            src={
              data.backdrop_path !== null
                ? `https://image.tmdb.org/t/p/w500${data.backdrop_path}`
                : `https://image.tmdb.org/t/p/w500${data.poster_path}`
            }
            alt=""
          />
          <div className="infoLay">
            <h6>{data.original_title}</h6>
            <div className="buttonBox">
              <button
                onClick={() => {
                  handleButtonClick("tellier");
                }}
              >
                <i className="fa-solid fa-play"></i> Play Now
              </button>
              <div className="icons">
                <span>
                  <i className="fa-solid fa-plus"></i>
                </span>
                <span>
                  <i className="fa-solid fa-thumbs-up"></i>
                </span>
                <span>
                  <i className="fa-solid fa-volume-high"></i>
                </span>
              </div>
            </div>
          </div>
        </div>
        <div className="minMovieInfo">
          <h6>{data.original_title}</h6>
          <div className="buttonBox">
            <button
              onClick={() => {
                handleButtonClick("tellier");
              }}
            >
              <i className="fa-solid fa-play"></i> Play Now
            </button>
            <div className="icons">
              <span>
                <i className="fa-solid fa-plus"></i>
              </span>
              <span>
                <i className="fa-solid fa-thumbs-up"></i>
              </span>
              <span>
                <i className="fa-solid fa-volume-high"></i>
              </span>
            </div>
          </div>
        </div>

        <div className="movieInfo">
          <div className="leftSide">
            <div className="Description">
              <h4>Description</h4>
              <p>{data.overview}</p>
            </div>
            <div className="Cast">
              <h4>Cast</h4>
              <div className="pictures">
                <div className="box">
                  <img
                    src={`https://image.tmdb.org/t/p/w300${data.poster_path}`}
                    alt=""
                  />
                </div>
                <div className="box">
                  <img
                    src={`https://image.tmdb.org/t/p/w300${data.poster_path}`}
                    alt=""
                  />
                </div>
                <div className="box">
                  <img
                    src={`https://image.tmdb.org/t/p/w300${data.poster_path}`}
                    alt=""
                  />
                </div>
              </div>
            </div>
            <div className="Reviews">
              <div className="infoBox">
                <h4>Reviews</h4>
                <div className="addBotton">
                  <i class="fa-solid fa-plus"></i> Add Your Review
                </div>
              </div>
              <div className="reviewsBox">{<MoviesReviews />} </div>
            </div>
          </div>

          <div className="rightSide">
            <h4>
              <i class="fa-solid fa-calendar-days"></i> Released Year
            </h4>
            <p>{data.release_date}</p>
            <h4>
              <i class="fa-solid fa-language"></i>
              Available Languages
            </h4>
            <span className="lang">English</span>
            <span className="lang">arabic</span>
            <span className="lang">germany</span>
            <span className="lang">france</span>

            <h4>
              <i class="fa-regular fa-star"></i>Ratings
            </h4>
            <div className="ratingBox">
              <div className="box">
                <h4>IMDB</h4>
                {data.vote_average}
                <i class="fa-solid fa-star"></i>
              </div>
            </div>
            <h4>
              <i class="fa-solid fa-vector-square"></i>Gernes
            </h4>
            <div className="gernes">
              {data?.genres?.map((el) => {
                return (
                  <>
                    <span>{el.name}</span>
                  </>
                );
              })}
            </div>
            <h4>Director</h4>
            <div className="picture">
              <img src="" alt="" />
              <div className="info">
                {/* <h6>ahmed</h6>
                <p></p> */}
              </div>
            </div>
            <h4>Music</h4>
            <div className="picture">
              <img src="" alt="" />
              <div className="info">
                {/* <h6>ahemd</h6>
                <p></p> */}
              </div>
            </div>
          </div>
        </div>
        <div className="tellier" id="tellier">
          {videoAvail === true ? (
            <>
              <ReactPlayer
                className="video"
                id="video"
                url={youtubeUrl}
              ></ReactPlayer>
            </>
          ) : (
            <>
              <div className="notAvelable" id="notAvelable">
                Video Is Not Available
              </div>
            </>
          )}
        </div>
      </div>
      <FreeTrealBox />
    </>
  );
}
