import React, { useContext, useEffect, useRef, useState } from "react";
import "./SliderComponentStyle.scss";
import OwlCarousel from "react-owl-carousel";
import "owl.carousel/dist/assets/owl.carousel.min.css";
import "owl.carousel/dist/assets/owl.theme.default.min.css";
import { MovieContext } from "../../../context/movieContext";
import { Link } from "react-router-dom";

export default function SliderComponent({ content, title }) {
  let [data, setData] = useState([]);
  let [flag, setFlag] = useState(false);
  const { getData } = useContext(MovieContext);
  const carouselRef = useRef(null);

  async function getAllData() {
    let req = await getData(content).catch((err) => {});
    setData(req?.data?.results);
    setFlag(true);
  }

  useEffect(() => {
    getAllData();
  }, []);

  useEffect(() => {
    getAllData();
  }, [flag]);

  const handleDots = (mathMark) => {
    let dots = document.querySelectorAll(".custom-dot ");
    if (mathMark === "+") {
      for (let i = 0; i < dots.length; i++) {
        if (dots[i]?.classList.contains("active") === true && i < 2) {
          dots[i]?.classList.remove("active");
          dots[i + 1]?.classList.add("active");
          return;
        }
      }
    } else {
      for (let i = 0; i < dots.length; i++) {
        if (dots[i]?.classList.contains("active") === true && i !== 0) {
          dots[i]?.classList.remove("active");
          dots[i - 1]?.classList.add("active");
          return;
        }
      }
    }
  };
  const handleNext = () => {
    carouselRef.current?.next();
    handleDots("+");
  };
  const handlePrev = () => {
    carouselRef.current?.prev();
    handleDots("-");
  };
  function splitTitle(myTitle) {
    let splitedTitle = myTitle.split(" ");
    let concating = splitedTitle[0] + " " + splitedTitle[1];
    return concating;
  }

  return (
    <>
      <div className="SliderComponent">
        <div className="descBox">
          <div className="info">
            <h3 className="categoryTitle">{title}</h3>
          </div>
          <div className="nav">
            <button onClick={handlePrev}>
              <i class="fa-solid fa-arrow-left"></i>
            </button>
            <div class="custom-dots">
              <span class="custom-dot active"></span>
              <span class="custom-dot"></span>
              <span class="custom-dot"></span>
            </div>
            <button onClick={handleNext}>
              <i class="fa-solid fa-arrow-right"></i>
            </button>
          </div>
        </div>
        <OwlCarousel
          ref={carouselRef}
          className="owl-theme"
          loop
          dots={true}
          dotsEach={3}
          responsive={{
            0: {
              items: 1,
            },
            600: {
              items: 3,
            },
            1000: {
              items: 5,
            },
          }}
          autoWidth={true}
          margin={10}
        >
          {data?.map((element, index) => {
            return (
              <>
                <Link to={`/MovieDetails/${element.id}`}>
                  <div
                    className="item box"
                    onClick={() => {
                      console.log("heii");
                    }}
                  >
                    <div className="picture">
                      <div className="obactyLay"></div>
                      <img
                        src={`https://image.tmdb.org/t/p/w500${element.poster_path}`}
                        alt=""
                      />
                    </div>
                    <div className="info">
                      <span>{splitTitle(element.title)}</span>
                      <span>
                        <i class="fa-solid fa-arrow-right"></i>
                      </span>
                    </div>
                  </div>
                </Link>
              </>
            );
          })}
        </OwlCarousel>
      </div>
    </>
  );
}
