import React, { useContext, useEffect, useRef, useState } from "react";
import "./MovieAndShowsStyle.scss";
import Category from "../supComponents/Category/Category";
import SliderComponent from "../supComponents/SliderComponent/SliderComponent";
import FreeTrealBox from "../supComponents/FreeTrealBox/FreeTrealBox";
import OwlCarousel from "react-owl-carousel";
import { MovieContext } from "../../context/movieContext";
import { Link } from "react-router-dom";

export default function MoviesAndShows() {
  let [data, setData] = useState([]);
  let [flag, setFlag] = useState(false);
  let { getData } = useContext(MovieContext);
  const carouselRef = useRef(null);
  let [logding, setLoding] = useState(true);

  async function getAllData() {
    let req = await getData("popular").catch(() => {});
    setData(req?.data?.results);
    setFlag(true);
  }
  useEffect(() => {
    getAllData();
    setTimeout(() => {
      setLoding(false);
    }, 1000);
  }, []);
  useEffect(() => {
    getAllData();
  }, [flag]);

  const handleNext = () => {
    carouselRef.current?.next();
  };
  const handlePrev = () => {
    carouselRef.current?.prev();
  };
  return (
    <>
      {logding === true ? (
        <>
          <div className="logding">
            <span className="loader"></span>
          </div>
        </>
      ) : (
        <></>
      )}
      <div className="moviesSlider">
        <>
          <div className="container">
            <div className="arowLeft" onClick={handlePrev}>
              <i class="fa-solid fa-arrow-left"></i>
            </div>
            <div className="arowRight" onClick={handleNext}>
              <i class="fa-solid fa-arrow-right"></i>
            </div>
            <OwlCarousel
              ref={carouselRef}
              className="owl-theme test"
              loop
              dots={true}
              dotsEach={3}
              margin={10}
              center={true}
              items={1}
            >
              {data?.map((element) => {
                return (
                  <>
                    <div className="owl-itme">
                      <img
                        src={`https://image.tmdb.org/t/p/w500${element.backdrop_path}`}
                        alt=""
                      />
                      <div className="movieInfo">
                        <h3>{element.title}</h3>
                        <p>{element.overview}</p>
                        <Link to={`/movieDetails/${element.id}`}>
                          <button>Wach Now</button>
                        </Link>
                      </div>
                    </div>
                  </>
                );
              })}
            </OwlCarousel>
          </div>

          <div className="MovieAndShows">
            <Category></Category>
            <SliderComponent
              content={"now_playing"}
              title={"Now playing"}
            ></SliderComponent>
            <SliderComponent
              content={"popular"}
              title={"Popular"}
            ></SliderComponent>
            <SliderComponent
              content={"top_rated"}
              title={"Top rated"}
            ></SliderComponent>
            <SliderComponent
              content={"upcoming"}
              title={"Upcoming"}
            ></SliderComponent>
          </div>
          <FreeTrealBox></FreeTrealBox>
        </>
      </div>
    </>
  );
}
