import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";

export let getCategoryData = createAsyncThunk(
  "movie/getCategoryData",
  async () => {
    let req = await axios.get(
      "https://api.themoviedb.org/3/genre/movie/list?api_key=25cb3584002cde964aa80c9ad2c3b76f"
    );
    return req?.data.genres;
  }
);

export let searchMovieData = createAsyncThunk(
  "movie/searchMovieData",
  async (movieName = "fast") => {
    let req = await axios.get(
      `https://api.themoviedb.org/3/search/movie?api_key=25cb3584002cde964aa80c9ad2c3b76f&query=${movieName}`
    );
    return req?.data.results;
  }
);

export let getMoviesReviews = createAsyncThunk(
  "movie/getMoviesReviews",
  async (movieId) => {
    let req = await axios.get(
      `https://api.themoviedb.org/3/movie/${movieId}/reviews?api_key=25cb3584002cde964aa80c9ad2c3b76f`
    );
    return req?.data?.results;
  }
);
export let getMovieKey = createAsyncThunk(
  "movie/getMovieKey",
  async (movieId) => {
    let req = await axios.get(
      `https://api.themoviedb.org/3/movie/${movieId}/videos?api_key=25cb3584002cde964aa80c9ad2c3b76f`
    );
    return req?.data?.results;
  }
);

export let getMovieDetails = createAsyncThunk(
  "movie/getMovieDetails",
  async (movieId) => {
    let req = await axios.get(
      `https://api.themoviedb.org/3/movie/${movieId}?api_key=25cb3584002cde964aa80c9ad2c3b76f`
    );
    return req?.data;
  }
);
export let getMoviesByCategory = createAsyncThunk(
  "movie/getMoviesByCategory",
  async ([page,query] ) => {
    console.log(query);
    let req = await axios.get(
      `https://api.themoviedb.org/3/search/movie?api_key=25cb3584002cde964aa80c9ad2c3b76f&page=${page}&query=${query}`
    );
    return req?.data;
  }
);

let MovieSlice = createSlice({
  name: "movie",
  initialState: {
    categoryList: [],
    searchMovieList: [],
    movieDetails: {},
    moviesReviews: [],
    movieKeys: [],
    moviesCategory: [],
    page: 0,
  },
  reducers: {},
  extraReducers: (builder) => {
    builder.addCase(getCategoryData.pending, (state, action) => {});
    builder.addCase(getCategoryData.fulfilled, (state, action) => {
      state.categoryList = action.payload;
    });
    builder.addCase(getCategoryData.rejected, (state, action) => {});
    //=====
    builder.addCase(searchMovieData.pending, (state, action) => {});
    builder.addCase(searchMovieData.fulfilled, (state, action) => {
      state.searchMovieList = action.payload;
    });
    builder.addCase(searchMovieData.rejected, (state, action) => {});
    //=====
    builder.addCase(getMovieDetails.pending, (state, action) => {});
    builder.addCase(getMovieDetails.fulfilled, (state, action) => {
      state.movieDetails = action.payload;
    });
    builder.addCase(getMovieDetails.rejected, (state, action) => {});
    //=====
    builder.addCase(getMoviesReviews.pending, (state, action) => {});
    builder.addCase(getMoviesReviews.fulfilled, (state, action) => {
      state.moviesReviews = action.payload;
    });
    builder.addCase(getMoviesReviews.rejected, (state, action) => {});
    //=====
    builder.addCase(getMovieKey.pending, (state, action) => {});
    builder.addCase(getMovieKey.fulfilled, (state, action) => {
      state.movieKeys = action.payload;
    });
    builder.addCase(getMovieKey.rejected, (state, action) => {});
    //=====
    builder.addCase(getMoviesByCategory.pending, (state, action) => {});
    builder.addCase(getMoviesByCategory.fulfilled, (state, action) => {
      state.moviesCategory = action.payload.results;
      state.page = action.payload.page;
    });
    builder.addCase(getMoviesByCategory.rejected, (state, action) => {});
  },
});

export default MovieSlice.reducer;
