import React, { useEffect, useState } from "react";
import "./MoviesByCategoryStyle.scss";
import { useDispatch, useSelector } from "react-redux";
import { getMoviesByCategory } from "../../Redux/movieSlice";
import { Link, useParams } from "react-router-dom";
import FreeTrealBox from "../supComponents/FreeTrealBox/FreeTrealBox";

export default function MoviesByCategory() {
  let data = useSelector((state) => state?.movieLists.moviesCategory);
  let page = useSelector((state) => state?.movieLists.page);
  let { name } = useParams();
  let dispatch = useDispatch();
  let [logding, setLoding] = useState(true);

  useEffect(() => {
    dispatch(getMoviesByCategory([page, name]));
    if (page === 0) {
      page = 1;
    }
    setTimeout(() => {
      setLoding(false);
    }, 1000);
  }, []);

  useEffect(() => {
    dispatch(getMoviesByCategory([page, name]));
  }, [name]);

  function splitTitle(myTitle) {
    let splitedTitle = myTitle.split(" ");
    let concating = splitedTitle[0] + " " + splitedTitle[1];
    return concating;
  }
  const handelNunm = (num) => {
    dispatch(getMoviesByCategory([num, name]));
  };
  const handelprev = (prev) => {
    dispatch(getMoviesByCategory([prev, name]));
  };
  const handelNext = (next) => {
    if (next !== 7) {
      dispatch(getMoviesByCategory([next, name]));
    }
  };
  return (
    <>
      {logding === true ? (
        <>
          <div className="logding">
            <span className="loader"></span>
          </div>
        </>
      ) : (
        <></>
      )}
      <div className="moviesByCategory">
        <div className="container">
          <div className="boxContainer">
            {data.map((element) => {
              return (
                <>
                  {element.poster_path ? (
                    <>
                      <Link to={`/moviedetails/${element.id}`}>
                        <div className="box">
                          <img
                            src={`https://image.tmdb.org/t/p/w500${element.poster_path}`}
                            alt=""
                          />
                          <span>{splitTitle(element.title)}</span>
                        </div>
                      </Link>
                    </>
                  ) : (
                    <></>
                  )}
                </>
              );
            })}
          </div>
        </div>
        <FreeTrealBox />
        <div className="container">
          <div className="pagenation">
            <ul class="pagination">
              <li
                class="Previous"
                onClick={() => {
                  handelprev(page - 1);
                }}
              >
                <span aria-hidden="true">&laquo;</span>
              </li>
              <li
                class="page-item"
                onClick={() => {
                  handelNunm(page);
                }}
              >
                {page}
              </li>
              <li
                class="page-item"
                onClick={() => {
                  handelNunm(page + 1);
                }}
              >
                {page + 1}
              </li>
              <li
                class="page-item"
                onClick={() => {
                  handelNunm(page + 2);
                }}
              >
                {page + 2}
              </li>
              <li
                class="Next"
                onClick={() => {
                  handelNext(page + 1);
                }}
              >
                <span aria-hidden="true">&raquo;</span>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </>
  );
}
