import React, { useEffect, useState } from "react";
import "./plansStyle.scss";

export default function Plans() {
  let [price, setPrice] = useState([99.9, 110, 200]);
  let [plan, setPlan] = useState("/month");

  const handelPlan = (eI) => {
    let elemnts = document.querySelectorAll(".planBtn");
    let isActive = eI.target.getAttribute("class");
    if (eI.target == elemnts[0]) {
      setPrice([99.9, 110, 200]);
      setPlan("/month");
    } else {
      setPrice([60.5, 90, 150]);
      setPlan("/year");
    }
    if (isActive?.includes("active") === false) {
      eI.target.classList.add("active");
      for (let i = 0; i < elemnts.length; i++) {
        if (elemnts[i] !== eI.target) {
          elemnts[i].classList.remove("active");
        }
      }
    }
  };

  return (
    <>
      <div className="container">
        <div className="infoContainer">
          <div className="textContainer">
            <h3>Choose the plan that's right for you</h3>
            <p>
              Join StreamVibe and select from our flexible subscription options
              tailored to suit your viewing preferences. Get ready for non-stop
              entertainment!
            </p>
          </div>
          <div className="buttonContainer">
            <button className="planBtn active" onClick={handelPlan}>
              Monthly
            </button>
            <button className="planBtn" onClick={handelPlan}>
              Yearly
            </button>
          </div>
        </div>
        <div className="plansBoxContainer">
          <div className="Box">
            <h6>Basic Plan</h6>
            <p>
              Enjoy an extensive library of movies and shows, featuring a range
              of content, including recently released titles.
            </p>
            <div className="price">
              ${price[0]}
              <span> {plan} </span>
            </div>
            <div className="buttons">
              <button>Start Free Trial</button>
              <button>Choose Plan</button>
            </div>
          </div>
          <div className="Box">
            <h6>Standard Plan</h6>
            <p>
              Enjoy an extensive library of movies and shows, featuring a range
              of content, including recently released titles.
            </p>
            <div className="price">
              ${price[1]} <span>{plan}</span>
            </div>
            <div className="buttons">
              <button>Start Free Trial</button>
              <button>Choose Plan</button>
            </div>
          </div>
          <div className="Box">
            <h6>Premium Plan</h6>
            <p>
              Enjoy an extensive library of movies and shows, featuring a range
              of content, including recently released titles.
            </p>
            <div className="price">
              ${price[2]} <span>{plan}</span>
            </div>
            <div className="buttons">
              <button>Start Free Trial</button>
              <button>Choose Plan</button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
