import React from "react";
import "./ComparisonTable.scss";
import { Link } from "react-router-dom";

export default function ComparisonTable() {
  return (
    <>
      <div className="container">
        <div className="mainTitle">
          <h3>Compare our plans and find the right one for you</h3>
          <p>
            StreamVibe offers three different plans to fit your needs: Basic,
            Standard, and Premium. Compare the features of each plan and choose
            the one that's right for you.
          </p>
        </div>
        <div className="pricing-table">
          <table>
            <thead className="head">
              <tr>
                <th>Features</th>
                <th>Basic</th>
                <th>
                  Standard <span className="Popular">Popular</span>
                </th>
                <th>Premium</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>Price</td>
                <td>$9.99/mo</td>
                <td>$19.99/mo</td>
                <td>$49.99/mo</td>
              </tr>
              <tr>
                <td>Content</td>
                <td>
                  Access to a wide selection of movies and shows, including some
                  new releases.
                </td>
                <td>
                  Access to a wide selection of movies and shows, including some
                  new releases.
                </td>
                <td>
                  Access to a wide selection of movies and shows, including some
                  new releases.
                </td>
              </tr>
              <tr>
                <td>Devices</td>
                <td>Watch on one device simultaneously</td>
                <td>Watch on one device simultaneously</td>
                <td>Watch on one device simultaneously</td>
              </tr>
              <tr>
                <td>Free Trail</td>
                <td>7 Days</td>
                <td>9 Days</td>
                <td>11 Days</td>
              </tr>
              <tr>
                <td>Cancel Anytime</td>
                <td>Yes</td>
                <td>Yes</td>
                <td>Yes</td>
              </tr>
              <tr>
                <td>HDR</td>
                <td>No</td>
                <td>No</td>
                <td>Yes</td>
              </tr>
              <tr>
                <td>Dolby Atmos</td>
                <td>Yes</td>
                <td>No</td>
                <td>Yes</td>
              </tr>
              <tr>
                <td>Ad - Free</td>
                <td>No</td>
                <td>Yes</td>
                <td>Yes</td>
              </tr>
              <tr>
                <td>Offline Viewing</td>
                <td>No</td>
                <td>Yes, for select titles.</td>
                <td>Yes, for select titles.</td>
              </tr>
              <tr>
                <td>Family Sharing</td>
                <td>No</td>
                <td>Yes, up to 5 family members.</td>
                <td>Yes, up to 5 family members.</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </>
  );
}
