import React from "react";
import "./App.css";
import { RouterProvider, createHashRouter } from "react-router-dom";
import LayOut from "./components/LayOut/LayOut";
import HomePage from "./components/HomePage/HomePage";
import MovieDetails from "./components/MovieDetails/MovieDetails";
import { MovieContextProvider } from "./context/movieContext";
import { configStore } from "./Redux/store";
import { Provider } from "react-redux";
import SupportPage from "./components/SupportPage/SupportPage";
import MoviesAndShows from "./components/MoviesAndShows/MoviesAndShows";
import SubscriptionPage from "./components/SubscriptionPage/SubscriptionPage";
import MoviesByCategory from "./components/MoviesByCategory/MoviesByCategory";

function App() {
  const routers = createHashRouter([
    {
      path: "/",
      element: <LayOut />,
      children: [
        { index: true, element: <HomePage /> },
        { path: "/moviedetails/:id", element: <MovieDetails /> },
        { path: "/moviesandshows", element: <MoviesAndShows /> },
        { path: "/supportpage", element: <SupportPage /> },
        { path: "/subscriptionpage", element: <SubscriptionPage /> },
        { path: "/moviesbycategory/:name", element: <MoviesByCategory /> },
      ],
    },
  ]);
  return (
    <>
      <Provider store={configStore}>
        <MovieContextProvider>
          <RouterProvider router={routers}></RouterProvider>
        </MovieContextProvider>
      </Provider>
    </>
  );
}

export default App;
