import React, { useEffect, useRef } from "react";
import "./MoviesReviews.scss";
import OwlCarousel from "react-owl-carousel";
import "owl.carousel/dist/assets/owl.carousel.min.css";
import "owl.carousel/dist/assets/owl.theme.default.min.css";
import { useDispatch, useSelector } from "react-redux";
import { getMoviesReviews } from "../../../Redux/movieSlice";
import { useParams } from "react-router-dom";

export default function MoviesReviews() {
  let { id } = useParams();
  let data = useSelector((state) => state.movieLists.moviesReviews);
  let dispatch = useDispatch();

  useEffect(() => {
    dispatch(getMoviesReviews(id));
  }, []);

  const carouselRef = useRef(null);
  const handleDots = (mathMark) => {
    let dots = document.querySelectorAll(".custom-dot ");
    if (mathMark === "+") {
      for (let i = 0; i < dots.length; i++) {
        if (dots[i]?.classList.contains("active") === true && i < 2) {
          dots[i]?.classList.remove("active");
          dots[i + 1]?.classList.add("active");
          return;
        }
      }
    } else {
      for (let i = 0; i < dots.length; i++) {
        if (dots[i]?.classList.contains("active") === true && i !== 0) {
          dots[i]?.classList.remove("active");
          dots[i - 1]?.classList.add("active");
          return;
        }
      }
    }
  };
  const handleNext = () => {
    carouselRef.current?.next();
    handleDots("+");
  };
  const handlePrev = () => {
    carouselRef.current?.prev();
    handleDots("-");
  };
  const char = "l";
  const regex = new RegExp(char, "g");

  return (
    <>
      <div className="moviesReviews">
        <OwlCarousel
          ref={carouselRef}
          className="owl-theme"
          responsive={{
            0: {
              items: 1,
            },
            600: {
              items: 2,
            },
            1000: {
              items: 4,
            },
          }}
          autoWidth={true}
          margin={20}
          dots={false}
        >
          {data?.length == 0 ? (
            <>
              <div className="item box reviews">
                <div className="info">
                  <div>
                    <span>empty</span>
                  </div>
                  <div className="rating">
                    <i class="fa-regular fa-star"></i>
                    <i class="fa-regular fa-star"></i>
                    <i class="fa-regular fa-star"></i>
                    <i class="fa-regular fa-star"></i>
                    <i class="fa-regular fa-star"></i>
                  </div>
                </div>
                <p>The movie doesn't have ratings.</p>
              </div>
            </>
          ) : (
            <>
              {data?.map((element) => {
                return (
                  <>
                    <div className="item box reviews">
                      <div className="info">
                        <div>
                          <span>{element.author_details.name}</span>

                          <span>{element.author_details.username}</span>
                        </div>
                        <div className="rating">
                          <i class="fa-solid fa-star"></i>
                          <i class="fa-solid fa-star"></i>
                          <i class="fa-solid fa-star"></i>
                          <i class="fa-solid fa-star"></i>
                          <i class="fa-regular fa-star"></i>
                        </div>
                      </div>
                      <p>{element.content}</p>
                    </div>
                  </>
                );
              })}
            </>
          )}
        </OwlCarousel>
        <div className="nav">
          <button onClick={handlePrev}>
            <i class="fa-solid fa-arrow-left"></i>
          </button>
          <div class="custom-dots">
            <span class="custom-dot active"></span>
            <span class="custom-dot"></span>
            <span class="custom-dot"></span>
          </div>
          <button onClick={handleNext}>
            <i class="fa-solid fa-arrow-right"></i>
          </button>
        </div>
      </div>
    </>
  );
}
