import React, { useEffect, useState } from "react";
import Frequently from "../supComponents/Frequently/Frequently";
import FreeTrealBox from "../supComponents/FreeTrealBox/FreeTrealBox";
import "./SupportPageStyle.scss";

export default function SupportPage() {
  let [logding, setLoding] = useState(true);
  useEffect(() => {
    setTimeout(() => {
      setLoding(false);
    }, 1000);
  }, []);
  return (
    <div>
      <>
      {logding === true ? (
        <>
          <div className="logding">
            <span className="loader"></span>
          </div>
        </>
      ) : (
        <></>
      )}
        <div className="support">
          <div className="container">
            <div className="pictureSide">
              <div className="info">
                <h3>Welcome to our support page!</h3>
                <p>
                  We're here to help you with any problems you may be having
                  with our product.
                </p>
              </div>
              <div className="picture">
                <img src={require("../../assets/images/support.png")} alt="" />
              </div>
            </div>
            <div className="formSide">
              <form>
                <div>
                  <label htmlFor="name">First Name :</label>
                  <input
                    type="text"
                    name=""
                    id="name"
                    placeholder="First Name"
                  />
                </div>
                <div>
                  <label htmlFor="secondName">Second Name :</label>
                  <input
                    type="text"
                    name=""
                    id="secondName"
                    placeholder=" Second Name"
                  />
                </div>
                <div>
                  <label htmlFor="email">Email :</label>
                  <input
                    type="email"
                    name=""
                    id="email"
                    placeholder="Enter your Email"
                  />
                </div>
                <div>
                  <label htmlFor="phone">Phone :</label>
                  <input
                    type="tel"
                    name=""
                    id="phone"
                    placeholder="Enter your Phone"
                  />
                </div>

                <div>
                  <label htmlFor="textarea">message :</label>
                  <textarea
                    name=""
                    id="textarea"
                    placeholder="Enter your Message"
                  ></textarea>
                </div>
                <div className="button">
                  <div className="checkBox">
                    <input type="checkbox" name="" id="checkbox" />
                    <label htmlFor="checkbox">
                      I agree with Terms of Use and Privacy Policy
                    </label>
                  </div>
                  <button>Send Message</button>
                </div>
              </form>
            </div>
          </div>
        </div>
        <Frequently />
        <FreeTrealBox />
      </>
    </div>
  );
}
