import React, { useEffect, useRef } from "react";
import "./catergoryStyle.scss";
import OwlCarousel from "react-owl-carousel";
import "owl.carousel/dist/assets/owl.carousel.min.css";
import "owl.carousel/dist/assets/owl.theme.default.min.css";
import { useDispatch, useSelector } from "react-redux";
import { getCategoryData } from "../../../Redux/movieSlice";
import { Link } from "react-router-dom";

export default function Category() {
  const carouselRef = useRef(null);
  let data = useSelector((state) => state?.movieLists?.categoryList);
  let categorysImagesList = [
    "action2",
    "adventurer",
    "Animation",
    "comedy",
    "Crime",
    "Documentary",
    "Drama",
    "fameily",
    "Fantasy",
    "History",
    "Horror",
    "Music",
    "Mystery",
    "Romance",
    "Science Fiction",
    "Thriller",
    "Tv",
    "War",
    "Western",
  ];
  let dispatch = useDispatch();

  useEffect(() => {
    dispatch(getCategoryData());
    // dispatch(searchMovieData());
  }, []);

  const handleDots = (mathMark) => {
    let dots = document.querySelectorAll(".custom-dot ");
    if (mathMark === "+") {
      for (let i = 0; i < dots.length; i++) {
        if (dots[i]?.classList.contains("active") === true && i < 2) {
          dots[i]?.classList.remove("active");
          dots[i + 1]?.classList.add("active");
          return;
        }
      }
    } else {
      for (let i = 0; i < dots.length; i++) {
        if (dots[i]?.classList.contains("active") === true && i !== 0) {
          dots[i]?.classList.remove("active");
          dots[i - 1]?.classList.add("active");
          return;
        }
      }
    }
  };
  const handleNext = () => {
    carouselRef.current?.next();
    handleDots("+");
  };
  const handlePrev = () => {
    carouselRef.current?.prev();
    handleDots("-");
  };

  return (
    <>
      <div className="category">
        <div className="descBox">
          <div className="info">
            <h3 className="categoryTitle">
              Explore our wide variety of categories
            </h3>
            <p className="categoryDescription">
              Whether you're looking for a comedy to make you laugh, a drama to
              make you think, or a documentary to learn something new
            </p>
          </div>
          <div className="nav">
            <button onClick={handlePrev}>
              <i class="fa-solid fa-arrow-left"></i>
            </button>
            <div class="custom-dots">
              <span class="custom-dot active"></span>
              <span class="custom-dot"></span>
              <span class="custom-dot"></span>
            </div>
            <button onClick={handleNext}>
              <i class="fa-solid fa-arrow-right"></i>
            </button>
          </div>
        </div>
        <OwlCarousel
          ref={carouselRef}
          className="owl-theme"
          loop
          dots={true}
          responsive={{
            0: {
              items: 1,
            },
            600: {
              items: 3,
            },
            1000: {
              items: 5,
            },
          }}
          autoWidth={true}
          margin={10}
        >
          {data?.map((element, index) => {
            return (
              <>
               <Link to={`/MoviesByCategory/${element.name}`}>
               <div
                  className="item box"
                  onClick={() => {
                    console.log("heii");
                  }}
                >
                  <div className="picture">
                    <div className="obactyLay"></div>
                    <img
                      src={require(`../../../assets/images/${categorysImagesList[index]}.jpg`)}
                      alt=""
                    />
                  </div>
                  <div className="info">
                    <span>{element.name}</span>
                    <span>
                      <i class="fa-solid fa-arrow-right"></i>
                    </span>
                  </div>
                </div>
               </Link>
              </>
            );
          })}
        </OwlCarousel>
      </div>
    </>
  );
}
